@import './assets/styles/colors.scss';
@import './assets/styles/fonts.scss';

*,
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  background-color: $colorBodySilver;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 24px;
}

button {
  cursor: pointer;
  outline: none;
  border: none;
  font-weight: 700;
}

.container {
  max-width: 1200px;
  margin: 0 auto;

  &__position {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-top: 100px;
  }
}
